<template>
    <div class="YesOrder">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
<!--      确认订单显示-->
      <div class="SpecificAddress both">
        <div class="icon_left"><i class="iconfont">&#xe600;</i></div>
        <div class="Home">
          <p>郭靖 <span>18212345675</span></p>
          <div>浙江省杭州市下城区安桥港街63号新天地商务中心二栋尚座西楼</div>
        </div>
        <div class="icon_right"><i class="iconfont ">&#xe67c;</i></div>
      </div>
<!--      购买的东西-->
      <div class="Shoppingthing">
<!--        物品-->
        <div class="stuff">
          <p class="Onimg"><img src="../../../assets/img/common/falseShow.png" alt=""></p>
          <div class="right">
            <div class="file cl_333 mb24"><p class="f34">#W2W-1620261</p><p class="f28">$361.00</p></div>
            <div class="file cl_666 mb14">
              <div class="more"><p><span class="span1">24</span><span class="span2">H</span></p>PCB 4cm*12cm 2层</div>
              <p>*2</p>
            </div>
            <div class="file material"><p class="">#W2W-1620261</p></div>
          </div>
        </div>
        <div class="stuff">
          <p class="Onimg"><img src="../../../assets/img/common/falseShow.png" alt=""></p>
          <div class="right">
            <div class="file cl_333 mb24"><p class="f34">#W2W-1620261</p><p class="f28">$361.00</p></div>
            <div class="file cl_666 mb14">
              <div class="more"><p><span class="span1">24</span><span class="span2">H</span></p>PCB 4cm*12cm 2层</div>
              <p>*2</p>
            </div>
            <div class="file material"><p class="">#W2W-1620261</p></div>
          </div>
        </div>
<!--        详情-->
        <div class="inDetail">
          <div class="row"> <p>税费:</p> <p class="cl_f90">$6.00</p></div>
          <div class="row pr"> <p>快递费:  <i class="iconfont cl_7C5922 follow" >&#xe653;</i></p> <p class="cl_f90">$6.00</p>
          <div class="bubble" ><span >12345678910我是一只小小鸟飞呀飞的搞 </span>
            <div> <i  class="iconfont f40" @click="BubblesShow=!BubblesShow">&#xe634;</i></div>
           </div>
          </div>
          <div class="row"  @click="ExpressShow=true"> <p>快递方式:</p> <p class="">{{ExpressText.name}} <span class="cl_f90">{{ExpressText.money}}</span> <i class="iconfont cl_ccc">&#xe67c;</i></p></div>
          <div class="row"> <p>需要发票:</p> <p class="need">
            <span  v-for="(data,index) in invoice" @click="invoiceIndex=index" :key="index" :class="invoiceIndex==index?'active':''">{{data.name}}</span>
          </p></div>
          <div class="row"> <p>包装盒:</p> <p class="">$6.00</p></div>
          <div class="row"> <p>发货单:</p>
            <p class="need">
            <span class="" v-for="(data,index) in dispatchList" @click="dispatchListIndex=index" :key="index" :class="dispatchListIndex==index?'active':''">{{data.name}}</span>
          </p></div>
          <div class="row" @click="CompanyShow=true"> <p>快递面单公司名:</p> <p class=""> {{CompanyText}}<i class="iconfont cl_ccc">&#xe67c;</i></p></div>
          <div class="row"> <p>订单包备注:</p> <p class=""><input type="text" placeholder="请输入"></p></div>
          <div class="row" @click="linkman"> <p>订单联系人:</p> <p class="">{{NowAccount}}<i class="iconfont cl_ccc">&#xe67c;</i></p></div>
        </div>
        <p class="f24 cl_999">*订单相关问题，捷配网将联系此联系人</p>

      </div>
      <div class="fixationBottom">
        <span class="dib vm">共3件</span>
        <p>合计：<span>￥722.00</span></p>
        <div class="buttom" @click="jump">付款(2)</div>
      </div>
      <van-action-sheet v-model="ExpressShow" :actions="ExpressActions" @select="ExpressonSelect" />
      <van-action-sheet v-model="CompanyShow" :actions="CompanyActions" @select="CompanySelect" />


    </div>

</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
      name: "OrderDetails",
      components: {HomePageTop},
      data() {
        return {
          //像子组件传值
          TopTotle: { name: "确认订单",left: true},
          //气泡显示
          BubblesShow:false,
          // 快递方式
          ExpressShow: false,
          ExpressActions: [{ name: "顺丰标快",money:"22.00" }, { name: "顺丰标快到付",money:"11.00"}, { name:"中通快递",money:"00.00" },],
          ExpressText:{},
          //公司名
          CompanyShow:false,
          CompanyActions: [{ name: "捷配"}, { name: "无"}],
          CompanyText:"捷配",
          //发货单
          dispatchList:[{name:"需要"},{name:"不需要"}],
          dispatchListIndex:"",
          //发票
          invoice:[{name:"需要"},{name:"不需要"}],
          invoiceIndex:"",
          //当前登录账号变量
          NowAccount:"当前登录账号",
        }
      },
      methods: {
        //联系人
        linkman(){
          this.$router.push({
            path:"Linkman"
          })
        },
        //快递方式
        ExpressonSelect(item) {
          // 默认情况下点击选项时不会自动收起
          // 可以通过 close-on-click-action 属性开启自动收起
          this.ExpressShow = false;
          this.ExpressText={name:item.name,money:"$"+item.money};
        },
        //公司名
        CompanySelect(item){
          this.CompanyShow = false;
          this.CompanyText=item.name

        },
        //跳转
        jump(){
          this.$router.push({
            path:"PayOrder"
          })


        },
      },
      created() {
        // console.log(this.$router.currentRoute.params.name);

        console.log(this.NowAccount);
        if(this.$router.currentRoute.params.name==undefined){

        }else{
          this.NowAccount=this.$router.currentRoute.params.name
        }
      }
    }

</script>

<style lang="less">
  .follow :hover .bubble{
    display: block;
  }
  .YesOrder{
    .Head{background: #fff};
    //修改地址
    .SpecificAddress{
      margin-top: .24rem;background: #fff;padding: 0 .3rem;height: 2.21rem;
      .icon_left{
        float: left;    height: 100%;line-height:2.21rem;
        .iconfont{font-size: .36rem;color: #f90;vertical-align: middle;display: inline-block;}
      }
      .Home{
        display: inline-block;vertical-align: middle;width: 6.2rem;padding-top: .3rem;padding-left: .24rem;
        p{font-size: .34rem;color: #333; span{color: #999;font-size: .24rem;display: inline-block;}}
        div{width: 100%;font-size: .28rem;color: #333;margin-top: .26rem}
      }
      .icon_right{
        float: right;    height: 100%;line-height:2.21rem;
        .iconfont{font-size: .34rem;color: #CCCCCC;}
      }
    }
    //具体详情
    .Shoppingthing{
      background: #fff;
      margin-top: .24rem;
      width: 100%;

      padding: .3rem;

      margin-bottom: 1.6rem;
      /*具体事务*/
      .stuff{
        padding-bottom: .4rem;
        .Onimg{width: .62rem;height: .62rem;display: inline-block;vertical-align: top;
          img{width: 100%;height: 100%} }
        .right{
          display: inline-block;
          width: 6rem;
          vertical-align: middle;
          margin-left: .2rem;
          .file{display: flex;    justify-content: space-between;}
          .more{
            p{margin-right: .2rem; position: relative;width: .85rem;height: .34rem;line-height: .34rem;border: 1px solid red;display: inline-block;vertical-align: bottom; .span1{width: .41rem;position: absolute;left: 0;height: .34rem;text-align: center;              display: inline-block;color: red} .span2{width: .41rem;position: absolute;right: 0;height: .34rem;display: inline-block;background: red;color: #fff;text-align: center;}    }
          }
          .material{width: 5.1rem;height: .57rem;line-height: .57rem;color: #666666;font-size: .28rem;background: #F9F9F9;padding: 0 .3rem 0 .15rem}

        }

      }

      .inDetail{
        color: #666;font-size: .28rem;
        .row{display: flex;justify-content: space-between;
          margin-bottom: .38rem;
          input{border: none;text-align: right}
          .need{
           span{
             width: 1.12rem;height: .5rem;line-height: .5rem;border: 1px solid #D1D3D8;
             color: #333;font-size: .28rem;display: inline-block;text-align: center;
             margin-left: .16rem;
           }
          }

        }

      }

    }
    //付款
    .fixationBottom{font-size: .24rem;
      box-shadow: 0px -3px 5px 0px rgba(0, 0, 0, 0.12);
      color: #999999;
      width: 100%;height: 1.12rem;line-height: 1.12rem;bottom: 0;position: fixed;background: #fff;padding: 0 .3rem;font-size: .28rem;clear: bottom;
      text-align: right;
      .buttom{width:1.62rem;height: .66rem;text-align: center;line-height: .66rem;background: #f90;display: inline-block;color: #fff;font-size: .28rem}
      p{
        font-size: .28rem;
        vertical-align: middle;
        display:inline-block;
        span{
          font-size: .34rem;
          color: #f90;
        }
      }
    }
  }

  .YesOrder .Shoppingthing .inDetail .row .need .active{border: 1px solid #f90}
  /*.fixationBottom  .right .AddRemove{width: 1.2rem;height: .66rem;line-height: .66rem;border: 1px solid #f90;text-align: center;font-size: .28rem;color: #f90;    display: inline-block;vertical-align: middle}*/
  /*.fixationBottom{}*/
  /*.fixationBottom .right .buttom{width: 1.62rem;height: .66rem;line-height: .66rem;text-align: center;color: #fff;font-size: .28rem;display: inline-block}*/
  /*.fixationBottom .icon_show{width: .4rem;height: .4rem;display: inline-block;vertical-align: middle;}*/

</style>



